import { Box, Typography,styled ,Button} from "@mui/material";
import { useNavigate } from "react-router-dom";
const Component = styled(Box)`
height:65vh;
width:80%;
background:#fff;
margin:80px 140px;

`;
const Container = styled(Box)`
text-align:center;
padding-top:70px;
`;
const Image = styled('img')({
    
})
const Text = styled(Typography)`
font-weight:600;
background:		#A8A8A8;
color:#fff;
font-family:Bodoni MT;

`



const Order = () => {
    const navigate = useNavigate();
    function Gohome(){
        navigate('/');
    }
    const imgurl = 'https://www.wordstream.com/wp-content/uploads/2022/07/full-size-thank-you-for-your-order-images-13.png';
    return (
        <Component>
            <Container>
                <Image src={imgurl} alt="empty" style={{width:'35%',}} />
                <Text>Purchased</Text>
                <Text>Buy more items.....<Button onClick={Gohome} style={{ background:'#76ABDF',fontWeight:'600',color:'white'}}>Go To Homepage</Button></Text>
               
            </Container>


        </Component>
    )
}
export default Order;